* {
    margin: 0;
    padding: 0;
}

body {
    width: 100vw;
    height: 100vh;
}

#root {
    width: 100%;
    height: 100%;
}

::-webkit-scrollbar {
    width: 6px;
    height: 8px;
    background-color: #fff;
}

::-webkit-scrollbar-thumb {
    background: #aaa;
    min-height: 40px;
}
